/* Intro */

.intro {
    color: #fff;
    min-height: 100vh;
    background: url('/build/img/intro__icon/intro-bg.jpg') center no-repeat;
    background-size: cover;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}




.intro__subtitle {
    padding-top: 15%;
    margin-bottom: 20px;
    font-size: 72px;
    font-family: 'Kaushan Script', cursive;

}

.intro__title {
    text-transform: uppercase;
    font-size: 150px;
    font-weight: 700;
    line-height: 0.93;
}

.intro__title::after {
    content: "";
    display: block;
    width: 60px;
    height: 3px;
    background: #fff;
    margin: 60px auto;
}

.btn {
    display: inline-block;
    font-weight: 700;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    margin: 0 auto;
    border: 3px solid #fff;
    padding: 9px 39px;
    text-decoration: none;
    transition: background-color .1s linear,
        color .1s linear;

    &:hover {
        background-color: #fff;
        color: #333;
    }
}

/* Slider */

.slider {
    padding-top: 10%;
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;
    list-style: none;
    text-transform: uppercase;
    position: relative;
    font-size: 18px;
    margin-top: 30px;
}

.slider__number {
    font-size: 24px;
    font-weight: 700;
}

.slider__item {
    padding-top: 20px;
    width: 23%;
    opacity: .7;
    border-top: 3px solid #fff;
    position: relative;
}

.slider__item.active {
    opacity: 1;
}

.slider__item.active:before {
    content: "";
    display: block;
    width: 70px;
    height: 3px;
    position: absolute;
    top: -3px;
    left: 0;
    z-index: 1;
    background: #f38181;
}