body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 15px;
  color: #333;
  line-height: 1.6; }

*,
*:before,
*:after {
  box-sizing: border-box; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0; }

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  height: 100%; }

/* Intro */
.intro {
  color: #fff;
  min-height: 100vh;
  background: url("/build/img/intro__icon/intro-bg.jpg") center no-repeat;
  background-size: cover;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.intro__subtitle {
  padding-top: 15%;
  margin-bottom: 20px;
  font-size: 72px;
  font-family: 'Kaushan Script', cursive; }

.intro__title {
  text-transform: uppercase;
  font-size: 150px;
  font-weight: 700;
  line-height: 0.93; }

.intro__title::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background: #fff;
  margin: 60px auto; }

.btn {
  display: inline-block;
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  margin: 0 auto;
  border: 3px solid #fff;
  padding: 9px 39px;
  text-decoration: none;
  transition: background-color .1s linear, color .1s linear; }
  .btn:hover {
    background-color: #fff;
    color: #333; }

/* Slider */
.slider {
  padding-top: 10%;
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
  list-style: none;
  text-transform: uppercase;
  position: relative;
  font-size: 18px;
  margin-top: 30px; }

.slider__number {
  font-size: 24px;
  font-weight: 700; }

.slider__item {
  padding-top: 20px;
  width: 23%;
  opacity: .7;
  border-top: 3px solid #fff;
  position: relative; }

.slider__item.active {
  opacity: 1; }

.slider__item.active:before {
  content: "";
  display: block;
  width: 70px;
  height: 3px;
  position: absolute;
  top: -3px;
  left: 0;
  z-index: 1;
  background: #f38181; }

/* Header */
.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 1000; }

.header__inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-height: 100px; }

.header__logo {
  width: 50%;
  font-size: 30px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700; }
  .header__logo a {
    list-style: none;
    text-decoration: none;
    color: #fff;
    transition: color .2s linear; }
    .header__logo a:hover {
      color: #fce38a; }

.header__nav {
  width: 50%;
  font-size: 14px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  text-transform: uppercase; }

.header__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none; }

.nav__link {
  color: #fff;
  text-decoration: none;
  transition: color .1s linear;
  position: relative; }
  .nav__link:hover {
    color: #fce38a; }

.nav__link::after {
  content: "";
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fce38a;
  opacity: 0;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
  transition: opacity .1s linear; }

.nav__link:hover:after,
.nav__link.active:after {
  opacity: 1; }

.nav__link.active {
  color: #fce38a; }
